@import url('https://fonts.googleapis.com/css?family=Poiret+One|Tajawal');

body {
  font-family: 'Tajawal', sans-serif;
  margin: 0;
  padding: 0;
  font-weight: 100;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.chennaibg{
  width: 351px;
}

p, h3, h4, h2, h1, h5 {
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
#root {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}


