.displaydetailcard{
    background-color: whitesmoke;
    margin: 2%;
    padding: 4%;
    -webkit-box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24),
    0 1.5px 6px rgba(0, 0, 0, 0.12);
            box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24),
    0 1.5px 6px rgba(0, 0, 0, 0.12);
    overflow: auto;
    border-radius: 2px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.navswitch{
    display: none;
}

.displaydetailTitle{
    font-size: 40px;
    text-align: right;
    font-weight: lighter;
}

.asadevdetails > h1,h3 {
    font-weight: lighter;
}

.width100{
    width: 100%;
}

.courgette{
    font-size: 25px;
}

.devImage{
    float: right;
    font-size: 50px;
}

.blurredImage{
    -webkit-filter: blur(.8px);
            filter: blur(.8px);
}

.chnBoard{
    font-size: 50px;
    text-align: center;
    background-color: #0091CF;
    color: #212121;
}

.chnBoard > span:nth-child(-n+5){
    color: #EBF0F6;
    text-shadow: 2px 2px 1px #01010180;
}

.alignRight{
    text-align: right;
}

.uniqueList{
    padding: 10px;
}

.uniqueList > div{
    height: 40px;
    font-size: 20px;
}

.uniqueList > div::before{
    color: #00000038;
    content: "\25CF\2015";
    display: inline;
    vertical-align: initial;
}

.chennaiBG{
    margin-bottom: -17px;
    padding-bottom: 0px;
}


