.navholderStyle {
    font-size: x-large;
    padding-top: 5%;
    padding-bottom: 5%;
    padding-left: 5%;
}

.block {
    background: #eeeeee;
    color: black;
    background-image: -webkit-gradient(linear,right top, left top,from(transparent), color-stop(50%, transparent), color-stop(50%, #fd4445), to(#fd4445));
    background-image: -webkit-linear-gradient(right,transparent, transparent 50%, #fd4445 50%, #fd4445);
    background-image: -o-linear-gradient(right,transparent, transparent 50%, #fd4445 50%, #fd4445);
    background-image: linear-gradient(to left,transparent, transparent 50%, #fd4445 50%, #fd4445);
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    background-position: 100% 0;
    background-size: 200% 100%;
}

.block:hover {
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    background-position: 0 0;
    color: white;
    cursor: pointer;
}

.holderStyle{
    font-size: x-large;
    padding-top: 4%;
    padding-bottom: 4%;
    padding-left: 5%;
}


