@media only screen and (max-width: 450px){
    #root{
        display: block !important;
    }
    .mainProfileStyle,
    .navigatorHolder,
    .displayDetails {
        position: absolute;
        width: -webkit-fill-available;
        width: -moz-available;
        width: stretch;
        height: -webkit-fill-available;
        height: -moz-available;
        height: stretch;
        -webkit-transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
        -o-transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
        transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    .navswitch{
        display: block;
        width: 50px;
        height: 50px;
        background-color: #fd4445;
        position: fixed;
        bottom: 40px;
        right: 30px;
        border-radius: 50%;
        -webkit-box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24),
        0 1.5px 6px rgba(0, 0, 0, 0.12);
                box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24),
        0 1.5px 6px rgba(0, 0, 0, 0.12);
        -webkit-transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
        -o-transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
        transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    .holder > p{
        text-align: left;
    }

    .holder > img{
        float: right;
    }

    .displayDetails {
        z-index: 0;
    }

    .navigatorHolder {
        display: none;
        opacity: 0;
        z-index: 3;
    }

    .displayIt{
        z-index: 1;
        display: block;
        opacity: 1;
    }

    .mainProfileStyle{
        display: block;
        opacity: 1;
        z-index: 1;
    }

    .mainTogglr{
        z-index: 2;
    }
    .subTogglr{
        background-color: #0091CF !important;
        z-index: 1;
        opacity: 0;
    }

    .invertSvgarrow{
        -webkit-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
                transform: rotate(180deg);
        bottom: 15px;
    }

    .navswitchHolder{
        display: grid;
        /* padding: 5px; */
    }

    .bottomArrow, .topArrow{
        margin-left: auto;
        margin-right: auto;
    }

    .topArrow{
        -webkit-transform: translate(0px, 7px);
            -ms-transform: translate(0px, 7px);
                transform: translate(0px, 7px);
    }

    .bottomArrow{
        -webkit-transform: translate(0px, -3px);
            -ms-transform: translate(0px, -3px);
                transform: translate(0px, -3px);
    }
}


