.shareContent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding-left: 5%;
    padding-top: 5%;
    padding-bottom: 5%;
}
.shareHolder {
    -ms-flex: 1 1;
        flex: 1 1;
    margin: auto;
}

.shareIcon {
    width: 15%;
    vertical-align: middle;
    margin-left: 3%;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
    border-color: black;
    border-width: 0px;
    border-radius: 50%;
    padding: 10px;
    border-style: solid;
    background-color: #EEEE;
    -webkit-transition: all 0.35s ease-in;
    -o-transition: all 0.35s ease-in;
    transition: all 0.35s ease-in;
}

.shareIcon:hover{
    -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
    0 10px 10px rgba(0, 0, 0, 0.22);
            box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
    0 10px 10px rgba(0, 0, 0, 0.22);
    -webkit-transition: all 0.35s ease-in;
    -o-transition: all 0.35s ease-in;
    transition: all 0.35s ease-in;
}

.exportPdf{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 3%;
    -webkit-transition: all 0.35s ease-in;
    -o-transition: all 0.35s ease-in;
    transition: all 0.35s ease-in;
    background-color: #EEEE;
}

.exportPdf:hover{
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
    -webkit-transition: all 0.35s ease-in;
    -o-transition: all 0.35s ease-in;
    transition: all 0.35s ease-in;    
}

.marginAuto{
    margin: auto;
}

.pdfText{
    -ms-flex: 1 1;
        flex: 1 1;
    line-height: 1.2;
}

.pdfIcon{
    -ms-flex: 1 1;
        flex: 1 1;
}

.downloadSvg{
    width: 65%;
}

.pdfImage{
    padding-left: 15px;
}


